function apiMapper(client, provider) {
  if (provider === 'openai') {
      return {
      listAssistants: client.beta.assistants.list.bind(null),
      getAssistant: client.beta.assistants.retrieve,
      createAssistant: client.beta.assistants.create,
      listMessages: client.beta.assistants.listMessages,
      createThread: client.beta.threads.createThread,
      getThread: client.beta.threads.retrieveThread,
      getRun: null,
      submitToolOutputsToRun: client.beta.assistants.submitToolOutputs
  }
} else if (provider === 'azureai') {
  return {
    listAssistants: client.listAssistants,
    getAssistant: client.getAssistant,
    createAssistant: client.createAssistant,
    listMessages: client.listMessages,
    createThread: client.createThread,
    getThread: client.getThread,
    getRun: client.getRun,
    submitToolOutputsToRun: client.submitToolOutputsToRun
  }
}
}
export default apiMapper;
